/* NarratorClassSelector.css - RPG-inspired styling */

.narrator-class-selector {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  border: 1px solid var(--border-color);
}

.selector-header {
  text-align: center;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 15px;
}

.selector-header h3 {
  color: var(--accent-color);
  margin-bottom: 5px;
  font-size: 1.6rem;
  font-weight: 500;
}

.selector-header p {
  color: var(--text-color);
  font-size: 0.95rem;
  opacity: 0.85;
}

.class-categories {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 15px;
}

.class-category {
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.3), rgba(30, 30, 30, 0.3));
  border-radius: 8px;
  border: 1px solid rgba(122, 107, 72, 0.4);
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
}

.class-category:hover {
  border-color: var(--accent-color);
  box-shadow: 0 0 10px rgba(204, 156, 58, 0.3);
  transform: translateY(-2px);
}

.category-header {
  display: flex;
  align-items: center;
  padding: 12px;
  background: linear-gradient(to right, rgba(0,0,0,0.5), transparent);
}

.category-icon {
  font-size: 1.5rem;
  margin-right: 10px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.category-title {
  font-weight: bold;
  color: var(--accent-color);
  font-size: 1.1rem;
}

.class-list {
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-columns: 1fr;
  gap: 15px;
}

.narrator-class-card {
  background: linear-gradient(135deg, rgba(50, 50, 50, 0.4), rgba(20, 20, 20, 0.4));
  border-radius: 8px;
  padding: 15px;
  border: 1px solid rgba(122, 107, 72, 0.3);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.narrator-class-card:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  transform: translateY(-3px);
  border-color: var(--accent-color);
}

.class-card-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(204, 156, 58, 0.2);
}

.class-icon {
  font-size: 1.5rem;
  margin-right: 10px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}

.class-title {
  font-weight: bold;
  color: var(--accent-color);
  font-size: 1.2rem;
}

.class-description {
  color: var(--text-color);
  font-size: 0.9rem;
  margin-bottom: 15px;
  line-height: 1.5;
  min-height: 40px;
}

.select-class-button {
  background-color: var(--primary-color);
  color: var(--text-color);
  border: none;
  padding: 8px 16px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  width: 100%;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.select-class-button:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .class-categories {
    grid-template-columns: 1fr;
  }
  
  .class-list {
    max-height: 400px;
    overflow-y: auto;
  }
}